.App {
  text-align: center;

}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}



.App-header {
  background-image: linear-gradient(rgb(38, 158, 238),pink,white);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  font-size: calc(10px + 2vmin);
  color: black;
}
input{
  border: black  solid 3px;
  width: 200px;
  padding: 10px;
  border-radius: 50px;
}
input:hover{
  border: blue solid 3px;
  background-color: rgb(177, 246, 246);
}
.App-link {
  color: #61dafb;
}

.bordering{
  margin: 50px;
  width: 1300px;
  border: black solid 5px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  background-color: #09bef0;
  /* overflow-x: auto; */
}

.updates{
width: 500px;
/* height: 40px; */
margin: 30px;
display: block;
padding: 30px;
border: black solid 5px;
background-color: palevioletred;
border-radius: 50px;
  /* overflow-y: auto; */

}

.buttons{
  border: black  solid 3px;
  width: 200px;
  padding: 10px;
  border-radius: 50px;


}
.buttons:hover{
  border: blue solid 3px;
  background-color: rgb(177, 246, 246);
}
.buttonsstrick{
  position: sticky;
  top: 10px;
  left: 100px;
  right: 100px;
  text-align: center;
}